import React from "react"
import { DirectiveCard } from "./DirectiveCard"
import { SectionContainer, ContentContainer, Grid } from "./styled"
import UsersIcon from "@assets/svg/LucidIcons/users.svg"
import HeartsIcon from "@assets/svg/LucidIcons/heart.svg"
import ScrollIcon from "@assets/svg/LucidIcons/scroll.svg"
import BrainIcon from "@assets/svg/LucidIcons/brain.svg"
import BookIcon from "@assets/svg/LucidIcons/book.svg"
import SparklesIcon from "@assets/svg/LucidIcons/sparkles.svg"
import { Typography } from "@mui/material"

const DirectivesSection: React.FC = () => {
  return (
    <SectionContainer>
      <div className="header-container">
        <Typography variant="h1Bold" component={"div"} color="primary.main">
          Core Directives
        </Typography>
        <Typography variant="h4Medium" component={"div"}>
          These fundamental principles guide practitioners on their spiritual
          journey, fostering growth and transformation.
        </Typography>
      </div>
      <ContentContainer>
        <Grid>
          <DirectiveCard
            icon={<ScrollIcon />}
            title="Sacred Texts Study"
            content="Regular study and contemplation of sacred texts to deepen spiritual understanding and wisdom."
          />
          <DirectiveCard
            icon={<HeartsIcon />}
            title="Devotional Service"
            content="Engaging in selfless service and devotional practices to cultivate divine consciousness."
          />
          <DirectiveCard
            icon={<UsersIcon />}
            title="Community Engagement"
            content="Active participation in spiritual community activities and supporting fellow practitioners."
          />
          <DirectiveCard
            icon={<BrainIcon />}
            title="Mindful Living"
            content="Practicing mindfulness in daily activities and maintaining awareness of divine presence."
          />
          <DirectiveCard
            icon={<BookIcon />}
            title="Ethical Guidelines"
            content="Following moral and ethical principles that align with spiritual teachings."
          />
          <DirectiveCard
            icon={<SparklesIcon />}
            title="Spiritual Practices"
            content="Regular meditation, prayer, and other spiritual practices for inner growth."
          />
        </Grid>
      </ContentContainer>
    </SectionContainer>
  )
}

export default DirectivesSection
